/* Slider */
.slick-slider {
  position: relative;
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
/*! Flickity v2.2.0
https://flickity.metafizzy.co
---------------------------------------------- */
.flickity-enabled {
  position: relative;
}
.flickity-enabled:focus {
  outline: 0;
}
.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}
.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}
.flickity-enabled.is-draggable {
  -webkit-tap-highlight-color: transparent;
  tap-highlight-color: transparent;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}
.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.flickity-button {
  position: absolute;
  background: rgba(255, 255, 255, 0.75);
  border: none;
  color: #333;
}
.flickity-button:hover {
  background: #fff;
  cursor: pointer;
}
.flickity-button:focus {
  outline: 0;
  box-shadow: 0 0 0 5px #19F;
}
.flickity-button:active {
  opacity: 0.6;
}
.flickity-button:disabled {
  opacity: .3;
  cursor: auto;
  pointer-events: none;
}
.flickity-button-icon {
  fill: currentColor;
}
.flickity-prev-next-button {
  top: 50%;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  transform: translateY(-50%);
}
.flickity-prev-next-button.previous {
  left: 10px;
}
.flickity-prev-next-button.next {
  right: 10px;
}
.flickity-rtl .flickity-prev-next-button.previous {
  left: auto;
  right: 10px;
}
.flickity-rtl .flickity-prev-next-button.next {
  right: auto;
  left: 10px;
}
.flickity-prev-next-button .flickity-button-icon {
  position: absolute;
  left: 20%;
  top: 20%;
  width: 60%;
  height: 60%;
}
.flickity-page-dots {
  position: absolute;
  width: 100%;
  bottom: -25px;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
}
.flickity-rtl .flickity-page-dots {
  direction: rtl;
}
.flickity-page-dots .dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 8px;
  background: #333;
  border-radius: 50%;
  opacity: .25;
  cursor: pointer;
}
.flickity-page-dots .dot.is-selected {
  opacity: 1;
}
.header {
  position: fixed;
  width: 100%;
  z-index: 100;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  padding: 60px 0px;
  opacity: 0;
  padding-bottom: 0px;
  /*------------------*/
  /*--- ATTRIBUTES ---*/
  /*------------------*/
  /*----------------*/
  /*--- ELEMENTS ---*/
  /*----------------*/
  /*------------------*/
  /*--- RESPONSIVE ---*/
  /*------------------*/
}
.header.motion-in {
  opacity: 1;
}
.header.h-scrollDown .logo .text {
  opacity: 0;
}
.header.h-scrollUp .logo .text {
  opacity: 0;
}
.header.h-white:before {
  background: #fff;
}
.header.h-white.show-menu .hmbrg:before,
.header.h-white.show-menu .hmbrg:after {
  background: #000000;
}
.header.h-white.show-menu .hmbrg .text {
  color: #000000;
}
.header.h-white .hmbrg:before,
.header.h-white .hmbrg:after {
  background: #fff;
}
.header.h-white .logo .ico path {
  fill: #fff;
}
.header.h-white .logo .text path {
  fill: #fff;
}
.header.h-white .menu {
  color: #141515;
}
.header.h-white .menu .legals:before {
  background: #141515;
}
.header.show-menu:before {
  transform: scale(80);
}
.header.show-menu .hmbrg:before,
.header.show-menu .hmbrg:after {
  background: #fff;
}
.header.show-menu .hmbrg .text {
  color: #fff;
}
.header.show-menu .logo .ico path {
  fill: #FCD572;
}
.header.show-menu .logo .text path {
  fill: #fff;
}
.header:before {
  content: '';
  display: block;
  position: absolute;
  width: 50px;
  height: 50px;
  background: #141515;
  right: 50px;
  top: 43px;
  border-radius: 100%;
  transform: scale(0);
  transition: 0.7s all cubic-bezier(0.55, 0.085, 0, 0.99);
  z-index: -1;
}
.header .content {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.header .logo {
  width: 126px;
  height: 56px;
  background: url('../img/logo.svg') no-repeat center center;
  background-size: contain;
  cursor: pointer;
  z-index: 2;
}
.header .logo .ico path {
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  fill: #FCD572;
}
.header .logo .text {
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.header .logo .text path {
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  fill: #000000;
}
.header .hmbrg {
  position: relative;
  width: 30px;
  height: 15px;
  display: block;
  cursor: pointer;
  z-index: 2;
}
.header .hmbrg:hover:before {
  width: 80%;
}
.header .hmbrg:hover:after {
  width: 60%;
}
.header .hmbrg.active .text {
  opacity: 1;
  left: -180%;
}
.header .hmbrg.active:before {
  width: 100%;
  transform: rotate(45deg) translate(5px, 4px);
}
.header .hmbrg.active:after {
  width: 100%;
  transform: rotate(-45deg) translate(5px, -4px);
}
.header .hmbrg .text {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: block;
  left: -150%;
  font-family: "Blacker Sans Pro Book";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #000000;
  opacity: 0;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.header .hmbrg:before,
.header .hmbrg:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  top: 0px;
  display: block;
  right: 0px;
  background: #000000;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.header .hmbrg:after {
  bottom: 0px;
  top: auto;
  width: 80%;
}
@media (min-width: 3000px) {
  .header.show-menu:before {
    transform: scale(240);
  }
}
@media (min-width: 2000px) {
  .header.show-menu:before {
    transform: scale(130);
  }
}
@media (max-width: 1024px) {
  .header {
    height: 130px;
    padding: 40px 0px;
  }
  .header .logo {
    width: 96px;
    height: 46px;
  }
}
@media (max-width: 850px) {
  .header:before {
    right: 30px;
    top: 23px;
  }
}
@media (max-width: 425px) {
  .header {
    height: 36px;
    padding: 36px 0px;
  }
  .header .container-fluid {
    padding: 0px 30px;
  }
  .header .container-fluid .row {
    align-items: center;
    margin: 0px;
  }
  .header .container-fluid .row .col {
    padding: 0px;
    width: 100%;
  }
  .header:before {
    right: 0px;
    top: 20px;
  }
  .header .logo {
    width: 76px;
    height: 35px;
  }
  .header .hmbrg .text {
    font-size: 12px;
    left: -100%;
  }
  .header .hmbrg.active .text {
    left: -140%;
  }
}
.footer {
  position: relative;
  background: #141515;
  width: 100%;
  height: 100vh;
  display: flex;
  color: #fff;
  bottom: 0px;
  align-items: center;
  z-index: 3;
  /*------------------*/
  /*--- ATTRIBUTES ---*/
  /*------------------*/
  /*----------------*/
  /*--- ELEMENTS ---*/
  /*----------------*/
  /*------------------*/
  /*--- RESPONSIVE ---*/
  /*------------------*/
}
.footer.f-white {
  background: #fff;
  color: #000000;
}
.footer.f-white .f-infos {
  color: rgba(0, 0, 0, 0.5);
}
.footer.f-white .f-infos a:before {
  background: rgba(0, 0, 0, 0.5);
}
.footer.f-white .title {
  color: #141515;
}
.footer.f-white .button:not(.b-icon) {
  color: #000000;
}
.footer.f-white .button:not(.b-icon) .wrapper {
  border: 1px solid #000000;
}
.footer.f-white .button:not(.b-icon) .wrapper:before {
  background: #000000 !important;
}
.footer.f-white .button:not(.b-icon):hover span {
  color: #fff;
}
.footer.f-white .button:not(.b-icon) span {
  color: #000000;
  white-space: nowrap;
}
.footer.f-white .button:not(.b-icon) .background {
  background: #000000;
}
.footer.f-white .scrollup:before {
  background: #141515;
}
.footer.f-white .scrollup:hover .arrow svg path {
  fill: #fff !important;
}
.footer.f-white .scrollup .wrapper {
  border: 1px solid #000000;
  background: #fff !important;
}
.footer.f-white .scrollup .wrapper:before {
  background: #000000 !important;
}
.footer.f-white .scrollup .arrow svg path {
  fill: #141515 !important;
}
.footer.motion-in-1 .title {
  opacity: 1;
  transform: translateY(0px);
}
.footer.motion-in-1 .button:not(.b-icon) {
  opacity: 1;
  transform: translateY(0px);
}
.footer.motion-in-2 .f-menu {
  opacity: 1;
  transform: translateY(0px);
}
.footer.motion-in-2 .f-infos {
  opacity: 1;
  transform: translateY(0px);
}
.footer.motion-in-3 .scrollup:before {
  width: 100%;
}
.footer.motion-in-3 .scrollup .button {
  transform: translate(-50%, -50%);
  opacity: 1;
}
.footer.motion-in-3 .legals {
  opacity: 1;
}
.footer .container-fluid .row:nth-of-type(1) {
  padding-bottom: 120px;
}
.footer .container-fluid .row:nth-of-type(3) {
  position: absolute;
  width: 100%;
  bottom: 0px;
}
.footer .container-fluid .col.col-3 {
  display: flex;
  justify-content: center;
}
.footer .f-menu {
  width: 100%;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  transform: translateY(-30px);
  opacity: 0;
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.footer .f-menu.f-social {
  flex-wrap: nowrap;
  flex-direction: column;
  width: auto;
  float: right;
  transition-delay: .8s;
}
.footer .item {
  font-size: 16px;
  line-height: 40px;
  letter-spacing: 2px;
  font-family: "Blacker Sans Pro Book";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  width: 40%;
  cursor: pointer;
  opacity: 1;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.footer .item:hover {
  opacity: .2;
}
.footer .f-infos {
  font-family: "Blacker Sans Pro Book";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  color: rgba(255, 255, 255, 0.6);
  font-size: 16px;
  line-height: 30px;
  transform: translateY(-30px);
  transition-delay: .8s;
  opacity: 0;
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-delay: .4s;
}
.footer .f-infos a {
  position: relative;
  display: table;
}
.footer .f-infos a:hover:before {
  width: 100%;
}
.footer .f-infos a:before {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 0%;
  height: 1px;
  background: rgba(255, 255, 255, 0.6);
  bottom: 0px;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
  z-index: 1;
}
.footer .scrollup {
  position: relative;
  width: 100%;
  height: 50px;
}
.footer .scrollup:before {
  content: '';
  display: block;
  width: 0%;
  height: 1px;
  background: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.footer .scrollup .button {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  transform: translate(-50%, 0%);
  opacity: 0;
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-delay: .5s;
}
.footer .scrollup .button .wrapper {
  background: #141515;
}
.footer .legals {
  display: flex;
  justify-content: space-between;
  font-family: "Blacker Sans Pro Book";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  padding: 40px 0px;
  padding-top: 0px;
  opacity: 0;
  transition-delay: 1s;
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.footer .legals .l-mobile {
  display: none;
}
.footer .title {
  transform: translateY(-40px);
  opacity: 0;
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
}
.footer .button:not(.b-icon) {
  transform: translateY(-20px);
  opacity: 0;
  transition: 1s all cubic-bezier(0.55, 0.085, 0, 0.99);
  transition-delay: .5s;
}
@media (max-width: 1024px) {
  .footer .container-fluid:first-child {
    padding-top: 70px;
    padding-bottom: 40px;
  }
  .footer .container-fluid .row:nth-of-type(1) {
    padding-bottom: 50px;
  }
  .footer .f-infos br {
    display: none;
  }
}
@media (max-width: 736px) {
  .footer .item {
    font-size: 14px;
    line-height: 20px;
  }
  .footer .f-infos {
    font-size: 14px;
    line-height: 20px;
  }
  .footer .legals {
    padding-bottom: 20px;
