}
.contact .input {
  position: relative;
  width: 100%;
  margin-bottom: 30px;
}
.contact .input.add-focus::after {
  width: 100%;
}
.contact .input.add-focus span {
  transform: scale(0.6) translateY(-40px);
  opacity: .5;
}
.contact .input input {
  width: 100%;
  padding: 17px 0px;
  background: transparent;
  display: block;
  transition: 0.3s all cubic-bezier(0.55, 0.085, 0, 0.99);
  position: relative;
  font-size: 16px;
  font-family: "Blacker Sans Pro Book";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  z-index: 1;
}
.contact .input input:focus {
  outline: none;
}
.contact .input textarea {
  width: 100%;
  height: 180px;
  padding: 17px 0px;
  background: transparent;
  display: block;
  transition: 0.3s all cubic-bezier(0.55, 0.085, 0, 0.99);
  position: relative;
  font-size: 16px;
  font-family: "Blacker Sans Pro Book";
  font-style: normal;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  color: #fff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  z-index: 1;
}
.contact .input textarea:focus {
  outline: none;
}
.contact .input::after {
  content: '';
  display: block;
  position: absolute;
  width: 0%;
  height: 1px;
  background: #fff;
  bottom: 0px;
  z-index: 2;
  transition: 0.5s all cubic-bezier(0.55, 0.085, 0, 0.99);
